export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiButtonModule, NuiDocsModule, NuiSwitchModule, SrlcStage } from \"@nova-ui/bits\";\n\nimport {\n    SwitchDisableExampleComponent,\n    SwitchDocsExampleComponent,\n    SwitchSimpleExampleComponent,\n    SwitchTestComponent,\n    SwitchValueChangeExampleComponent,\n    SwitchVisualTestComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: SwitchDocsExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.ga,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"switch-test\",\n        component: SwitchTestComponent,\n    },\n    {\n        path: \"switch-visual-test\",\n        component: SwitchVisualTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiButtonModule,\n        NuiSwitchModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        SwitchDisableExampleComponent,\n        SwitchDocsExampleComponent,\n        SwitchSimpleExampleComponent,\n        SwitchTestComponent,\n        SwitchValueChangeExampleComponent,\n        SwitchVisualTestComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class SwitchModule {\n}\n";